import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo_default from '../../static/logo/logo_default.svg'

const Nav = styled.nav`
    position: relative;
    font-weight: 500;
    font-size: 1.3rem;
    @media (max-width: 400px) {
        font-size: 0.8rem;
    }
    transform: translateY(100%);
    height: 50px;
    background: transparent;
    display: flex;
    float: right;
    padding-left: 20px;
    padding-right: 20px;
    transition: all .25s ease-out;
    a {
        position: relative;
        font-family: "Domine";
        color: #1a1a1a;
        transition: all 0.25s ease-out
        @media (max-width: 500px) {
            padding-left: 0.7rem;
            padding-right: 0.7rem;
        }
        @media (max-width: 400px) {
            padding-left: 0.6rem;
            padding-right: 0.6rem;
        }
        padding-left: 1rem;
        padding-right: 1rem;
        overflow: hidden;
        div {
            position: relative;
            height: 2px;
            width: 0%;
            top: 0px;
            left: 50%;
            background-color: #a92228;
            transition: all 0.45s ease-out;
        }
        &:hover>div {
            width: 100%;
            left: 0%;
            background-color: #a92228;
            transition: all 0.45s ease-out;
        }
    }
`
const Logo = styled.img`
    position: relative;
    width: 350px;
    top: -120px;
    transition: all 0.25s ease-out;
    @media (max-width: 1000px) {
        transform: translateX(-10%);
    }
`

const StyledLink = styled(Link)`
    position: fixed;
    display: inline-block;
    transition: all 0.25s ease-out
`

const Pinned = function() {
    document.getElementById("logo_default").style.top = "-75px";
    document.getElementById("logo_default").style.width = "300px";
    document.getElementById("nav").style.transform = "translateY(150%)";
    document.querySelector(".headroom-wrapper").style.transform = "translateY(-50%)";
};

const unPinned = function() {
    document.getElementById("logo_default").style.height = "150px";
};
const unFixed = function() {
    document.getElementById("logo_default").style.width = "350px";
    document.getElementById("logo_default").style.top = "-120px";
    document.getElementById("nav").style.transform = "translateY(100%)";
    document.querySelector(".headroom-wrapper").style.transform = "translateY(0%)";
};

const NavBar = () => (
    <Headroom id="main_headroom" disableInlineStyles hide pin instantSnap
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={0} pinAnchor={0} hideAnchor={1000} >
        <StyledLink id="main-header-logo" to="/">
            <Logo id="logo_default" src={logo_default} alt='Dumna Niepodległa Logo' />
        </StyledLink>
        <Nav id="nav">  
            <Link to='/'>
                Wydarzenia
                <div />
            </Link>
            <Link to='/'>
                O nas
                <div />
            </Link>
            <Link to='/'>
                Kontakt
                <div />
            </Link>
            <Link to='/'>
                Sklep
                <div />
            </Link>
        </Nav>
    </Headroom>
);

export default NavBar

